import React from "react";
import "./Button.css";

const Button = (props) => {
  return (
    <button
      className={`btn ${props.className?props.className:''}`}
      type={props.type}
      onClick={props.onClick}
      style={props.style}
      ref={props.ref}
      id={props.id}
    >
      {props.children}
    </button>
  );
};

export default Button;
