import React, { Fragment, useState } from "react";
import "./ProductDetail.css";
import { useEffect } from "react";
import Carouesel from "react-material-ui-carousel";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";

import {
  clearErrors,
  getProductDetail,
  newReview,
} from "../../../redux/actions/productAction";
import {
  Button as MaterialButton,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@material-ui/core";
import { Rating } from "@material-ui/lab";

import Loader from "../../layout/Loader/Loader";
import Button from "../../Button/Button";
import Reviews from "./Reviews/Reviews";
import { useAlert } from "react-alert";
import MetaData from "../../layout/MetaData/MetaData";
import { addItemsToCart } from "../../../redux/actions/cartAction";
import { NEW_REVIEW_RESET } from "../../../redux/constants/productConstant";

const ProductDetail = () => {
  const dispatch = useDispatch();
  const { id } = useParams();
  const { product, loading, error } = useSelector(
    (state) => state.productDetail
  );
  const {success, error:reviewError } = useSelector(
    (state) => state.newReview
  );
  
  const alert = useAlert();
  const [quantity, setQuantity] = useState(1);
  const [open, setOpen] = useState(false);
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState("");

  const submitReviewToggle = async () => {
    open ? setOpen(false) : setOpen(true);
  };
  const reviewSubmitHandler = async () => {
    const myForm = new FormData();
    myForm.set("rating", rating);
    myForm.set("comment", comment);
    myForm.set("productId", id);

   await dispatch(newReview(myForm));
    dispatch(getProductDetail(id));
    submitReviewToggle();
  };

  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (reviewError) {
      alert.error(reviewError);
      dispatch(clearErrors());
    }

    if (success) {
      alert.success("Review is Submitted Successfully");
      dispatch({type:NEW_REVIEW_RESET});
    }
    dispatch(getProductDetail(id));
  }, [dispatch, id, error, alert,reviewError,success,product.rating]);

  const options = {
   
    value: product.ratings,
    size: "large",
    precision:0.5,
    readOnly:true
  };

  const increaseQuantity = () => {
    if (product.stock <= quantity) return;
    const qty = quantity + 1;
    setQuantity(qty);
  };
  const decreaseQuantity = () => {
    if (1 >= quantity) return;
    const qty = quantity - 1;
    setQuantity(qty);
  };
  const addToCartHandler = () => {
    dispatch(addItemsToCart(id, quantity));
    alert.success("Item Successfully Added to Cart");
  };

  return (
    <Fragment>
      <MetaData title={`${product.name}`} />
      {loading ? (
        <Loader />
      ) : (
        <div className="productDetail">
          <div className="carouesel">
            <Carouesel className="buttonVisible" stopAutoPlayOnHover={false}>
              {product &&
                product.images &&
                product.images.map((img, i) => (
                  <img
                    className="CaroueselImage"
                    src={`/${img}`}
                    key={img}
                    alt={`${i+1} Slide`}
                  />
                ))}
            </Carouesel>
          </div>
          <div>
            <div className="detailsBlock-1">
              <h2>{product.name}</h2>
              <p>Product #{product._id}</p>
            </div>
            <div className="detailsBlock-4">
              Description : <p>{product.description}</p>
            </div>
            <div className="detailsBlock-3">
              <h1>â‚¹ {product.price}</h1>
              <div className="detailsBlock-3-1">
                <div className="quantityBtn">
                  <button onClick={decreaseQuantity}>-</button>
                  <input readOnly type="number" value={quantity} />
                  <button onClick={increaseQuantity}>+</button>
                </div>
                <Button onClick={addToCartHandler}>Add To Cart</Button>
              </div>
              <p>
                Status:
                <b className={product.stock < 1 ? "redColor" : "greenColor"}>
                  {product.stock < 1 ? "Out Of Stock" : "In Stock"}
                </b>
              </p>
            </div>
            <div className="detailsBlock-2">
              <Rating {...options}  />
              <span className="detailsBlock-2-span">{`(${product.noOfReviews} Reviews)`}</span>
            </div>
            <Button onClick={submitReviewToggle} className="submitReviewBtn">
              Submit Review
            </Button>
            <Dialog
              aria-labelledby="simple-dialog-title"
              open={open}
              onClose={submitReviewToggle}
            >
              <DialogTitle>Submit Review</DialogTitle>
              <DialogContent className="submitDailog">
                <Rating
                  onChange={(e) => setRating(e.target.value)}
                  value={rating}
                />

                <textarea
                  className="submitDailogTextArea"
                  cols="30"
                  rows="5"
                  value={comment}
                  onChange={(e) => setComment(e.target.value)}
                ></textarea>
              </DialogContent>

              <DialogActions>
                <MaterialButton onClick={submitReviewToggle} color="secondary">
                  Cancel
                </MaterialButton>
                <MaterialButton onClick={reviewSubmitHandler} color="primary">
                  Submit
                </MaterialButton>
              </DialogActions>
            </Dialog>
          </div>
        </div>
      )}
      <Reviews product={product} />
    </Fragment>
  );
};

export default ProductDetail;
